
import { Vue } from "vue-class-component";
import { Prop } from "vue-property-decorator";
import { Task } from "../models/Task";

export default class AddTodo extends Vue {
  @Prop() tasks!: Task[];

  content = "";

  addHandeler() {
    this.$emit("addTask", new Task(this.content, false));
    this.content = "";
  }
}
